.footer {
  background-color: $light-grey;
  background-image: url("../Images/svg/footer-background-mobile.svg");
  background-repeat: no-repeat;
  background-position: center 100%;

  @media (min-width: $desktop-width) {
    background-image: url("../Images/svg/footer-background-desktop.svg");
  }
}

.footer__container {
  padding: 48px 16px;

  @media (min-width: $desktop-width) {
    padding: 70px 64px 80px 64px;
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
}

.footer__logo-link {
  margin-bottom: 24px;
  display: block;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }
}

.footer__description {
  display: block;
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    letter-spacing: normal;
  }
}

.footer__social-link {
  display: block;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: $black;

  &:hover {
    color: $background-green;
  }

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.7px;
  }
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  gap: 16px;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 0.9fr 1fr 1fr;
    gap: 40px 0;
  }
}

.footer__social-list {
  display: flex;
  gap: 12px;
}

.footer__social-item {
  position: relative;

  ::before {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%);
    left: -8px;
    width: 5px;
    height: 5px;
    background-color: $black;
    border-radius: 50%;
  }
}

.footer__social-item:nth-child(1) {
  ::before {
    display: none;
  }
}

.footer__navigation-link {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  color: $black;

  &:hover {
    color: $background-green;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    letter-spacing: normal;
  }
}

.footer__navigation-link--partners {
  border-bottom: 1px solid $black;

  &:hover {
    border-bottom: 1px solid $background-green;
  }
}

.footer__lepiohin-link {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  border-bottom: 3px solid $black;
  color: $black;
  padding-bottom: 2px;

  &:hover {
    color: $background-green;
    border-bottom: 3px solid $background-green;
  }
}

.footer__year {
  display: block;
  border-bottom: none;
  color: inherit;
  padding-top: 4px;
}
