.offer-email__wrapper {
  padding: 32px 37px 24px 16px;
  margin-bottom: 34px;
  background-image: url("/assets/images/svg/offer-background.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;

  @media (min-width: 335px) {
    background-color: $background-dark-green;
    border-radius: 16px;
  }

  @media (min-width: $desktop-width) {
    padding: 60px 95px 64px 64px;
    margin-bottom: 80px;
  }

  @media (min-width: 1350px) {
    background-color: transparent;
    border-radius: 0;
    background-image: url("/assets/images/svg/offer-background-desktop.svg");
  }
}

.offer-email__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin: 0 0 8px 0;
  color: $light-grey;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    letter-spacing: -1px;
    margin: 0 0 24px 0;
  }
}

.offer-email__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin: 0 0 24px 0;
  color: $light-grey;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
    margin: 0;
    width: 601px;
  }
}

.offer-email__container {
  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    gap: 48px;
  }
}

.offer-email__link {
  padding: 8px 18px 8px 8px;
  border-radius: 12px 32px 32px 12px;
  background-color: $light-grey;
  color: $black;
  display: block;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.5px;
  position: relative;

  &:hover {
    background-color: $background-grey;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 18px;
    width: 56px;
    height: 14px;
    background-image: url("../Images/svg/offer-arrow-mobile.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    padding: 20px 100px 20px 32px;
    flex-shrink: 0;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
    border-radius: 24px 102px 102px 24px;
    height: fit-content;
    min-width: 504px;

    &::after {
      width: 110px;
      height: 37px;
      right: 48px;
      background-image: url("../Images/svg/offer-email-arrow-desktop.svg");
    }
  }
}
