.intro {
  padding-top: 78px;
  
  @media (min-width: $desktop-width) {
    padding-top: 102px;
  }
}

.intro__wrapper {
  padding: 24px 0;
  background-color: $background-grey;
  border-radius: 16px;
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    padding: 90px 80px 100px 80px;
    margin-bottom: 120px;
    position: relative;
    background-color: transparent;

    ::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      border-radius: 32px;
      background-color: $background-grey;
      z-index: -2;
    }
  }
}

.intro__title {
  color: $black;
  font-size: 28px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: -0.7px;
  padding: 0 16px;

  @media (min-width: $desktop-width) {
    font-size: 72px;
    line-height: 110%;
    letter-spacing: -1px;
    width: 70%;
    padding: 0;
    margin-bottom: 16px;
  }
}

.intro__desktop-title {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.intro__mobile-title {
  display: block;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.intro__description {
  display: none;
  font-family: "Manrope";
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;
  letter-spacing: -0.3px;
  width: 540px;

  @media (min-width: $desktop-width) {
    display: block;
    margin-bottom: 80px;
  }
}

.intro__img {
  display: block;
  margin-bottom: 36px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $desktop-width) {
    position: absolute;
    margin: 0;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    z-index: -1;
    width: 50%;
    height: auto;
  }
}

.intro__link {
  padding: 11px 16px;
  border-radius: 12px 32px 32px 12px;
  background-color: $background-dark-green;
  display: block;
  color: $white;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin: 0 16px;
  position: relative;

  &:hover {
    background-color: rgba($background-dark-green, 0.7);
  }

  &:active {
    background-color: rgba($background-dark-green, 0.4);
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 32px;
    width: 65px;
    height: 14px;
    background-image: url("../Images/svg/intro-arrow.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    border-radius: 24px 102px 102px 24px;
    font-size: 32px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -1px;
    padding: 20px 180px 20px 32px;
    margin: 0;
    width: 431px;

    &::after {
      background-image: url("../Images/svg/intro-arrow-desktop.svg");
      width: 111px;
      height: 39px;
    }
  }
}
