.safety {
  margin-bottom: 64px;
}

.safety__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  text-align: center;
  margin: 0 0 12px 0;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin: 0 0 40px 0;
  }
}

.safety__list {
  display: flex;
  flex-direction: column;
  gap: 8px;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 873px;
    margin: 0 auto;
  }

  @media (min-width: 1440px) {
    display: grid;
    grid-template-columns: 320px 320px 320px 320px;
    width: 1440px;
  }
}

.safety__item {
  padding: 24px 16px;
  border-radius: 16px;
  background-color: $background-grey;
  width: 100%;

  &:nth-child(1) {
    border-radius: 16px 32px 32px 16px;
  }

  &:nth-child(3) {
    clip-path: polygon(0 0, 83% 0, 100% 17%, 100% 100%, 0 100%);
  }

  &:nth-child(4) {
    border-radius: 32px 16px 16px 16px;
  }

  @media (min-width: $desktop-width) {
    padding: 40px;
    display: flex;
    flex-direction: column;

    &:nth-child(1) {
      border-radius: 32px 90px 90px 32px;
    }

    &:nth-child(4) {
      border-radius: 90px 32px 32px 90px;
    }
  }
}

.safety__item-title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 4px;

  @media (min-width: $desktop-width) {
    margin-bottom: 8px;
  }
}

.safety__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin-bottom: 24px;
}

.safety__img {
  display: block;
  margin: 0 auto;
  width: 256px;
  height: 226px;

  &--intercom {
    @media (min-width: $desktop-width) {
      border-bottom-right-radius: 60px;
    }
  }

  @media (min-width: $desktop-width) {
    margin: auto auto 0 auto;
    width: 242px;
    height: 226px;
  }
}
