.cases {
  padding-bottom: 40px;
}

.cases__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  text-align: center;
  margin: 0 0 12px 0;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin: 0 0 40px 0;
  }
}

.cases__list {
  display: flex;
  flex-direction: column;
  gap: 8px;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    width: 873px;
    margin: 0 auto;
  }

  @media (min-width: 1440px) {
    width: 1440px;
  }
}
