.types__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  text-align: center;
  margin-bottom: 12px;
  
  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin: 0 0 40px 0;
  }
}

.types__wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 80px;
  }
}
