.case {
  padding: 24px 16px;
  border-radius: 16px 16px 32px 16px;
  background-color: $background-grey;

  &--plug {
    padding: 0;
    background-color: $white;
    width: 100%;
    height: 324px;
    background-image: url("../Images/svg/cases-plug.svg");
    background-position: center;
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    padding: 40px;
    border-radius: 32px;
    width: 432px;

    &:nth-child(1), &:nth-child(3) {
      border-radius: 32px 90px 90px 32px;
    }

    &:nth-child(4) {
      border-radius: 32px 90px 32px 32px;
    }

    &:nth-child(5) {
      border-radius: 32px 32px 90px 32px;
    }

    &--plug {
      background-image: url("../Images/svg/cases-plug-desktop.svg");
      height: 486px;
    }
  }
}

.case__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin: 0 0 4px 0;

  @media (min-width: $desktop-width) {
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
    margin: 0 0 8px 0;
  }
}

.case__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin: 0 0 24px 0;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
  }
}

.case__img {
  display: block;
  margin: 0 auto;
}
