.forbidden {
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    margin-bottom: 40px;
  }
}

.forbidden__wrapper {
  padding: 24px 16px 13px 16px;
  background-color: $background-beige;
  border-radius: 16px;

  @media (min-width: $desktop-width) {
    padding: 80px 60px 28px 60px;
    display: grid;
    grid-template-areas:
      "title description"
      "img description";
    grid-template-columns: 1fr 1fr;
    gap: 0 40px;
    border-radius: 32px;
  }
}

.forbidden__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    grid-area: title;
    font-size: 56px;
    line-height: 64px;
    margin-bottom: 36px;
  }
}

.forbidden__list {
  display: flex;
  flex-direction: column;
  gap: 16px;

  @media (min-width: $desktop-width) {
    grid-area: description;
    gap: 40px;
  }
}

.forbidden__item-title {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin-bottom: 8px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.7px;
  }
}

.forbidden__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    letter-spacing: normal;
  }
}

.forbidden__img {
  display: block;
  margin: 0 auto;

  @media (min-width: $desktop-width) {
    grid-area: img;
    margin: 0 auto 0 0;
  }
}
