.features {
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    margin-bottom: 80px;
  }
}

.features__wrapper {
  @media (min-width: $desktop-width) {
    max-width: 506px;

    &--reverse {
      order: 2;
    }
  }
}

.features__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  text-align: center;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin: 0 0 40px 0;
  }
}

.features__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.features__item {
  @media (min-width: $desktop-width) {
    display: flex;
    max-width: 1092px;
    gap: 40px;
    align-items: center;
    margin: 0 auto;
  }
}

.features__img {
  display: block;
  margin: 0 auto 16px auto;

  @media (min-width: $desktop-width) {
    margin: 0 auto;
  }

  &-reverse {
    @media (min-width: $desktop-width) {
      order: 2;
      margin: 0;
      margin: 0 auto;
    }
  }
}

.features__item-title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 8px;

  @media (min-width: $desktop-width) {
    font-size: 48px;
    line-height: 56px;
    letter-spacing: normal;
    margin-bottom: 16px;
  }
}

.features__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
  }
}
