.offer-warehouse__wrapper {
  background-image: url("/assets/images/svg/offer-background.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 32px 37px 24px 16px;
  margin-bottom: 64px;

  @media (min-width: 335px) {
    background-color: $background-dark-green;
    border-radius: 16px;
  }

  @media (min-width: $desktop-width) {
    padding: 60px 64px;
    margin-bottom: 80px;
  }

  @media (min-width: 1350px) {
    background-color: transparent;
    border-radius: 0;
    background-image: url("/assets/images/svg/offer-warehouse-background-desktop.svg");
  }
}

.offer-warehouse__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin: 0 0 8px 0;
  color: $light-grey;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    letter-spacing: -1px;
  }
}

.offer-warehouse__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin: 0 0 24px 0;
  color: $light-grey;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
    margin: 0 0 40px 0;
  }
}

.offer-warehouse__link {
  padding: 8px 18px 8px 8px;
  border-radius: 12px 32px 32px 12px;
  background-color: $light-grey;
  color: $black;
  display: block;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.5px;
  position: relative;

  &:hover {
    background-color: $background-grey;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 18px;
    width: 56px;
    height: 14px;
    background-image: url("../Images/svg/offer-arrow-mobile.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    padding: 34px 330px 34px 40px;
    font-size: 48px;
    line-height: 56px;
    letter-spacing: normal;
    border-radius: 24px 102px 102px 24px;
    width: 770px;

    &::after {
      background-image: url("../Images/svg/offer-warehouse-arrow-desktop.svg");
      width: 253px;
      height: 61px;
      right: 60px;
    }
  }
}
