.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transition: 500ms;
  backdrop-filter: blur(6px);
}

.header__wrapper {
  background-color: rgba($white, 0.9);
  padding-top: 14px;
  padding-bottom: 24px;
  display: flex;
  justify-content: end;
  align-items: center;
}

.header__logo-link {
  margin-right: auto;

  &:hover {
    opacity: 0.7;
  }
}

.header__list {
  display: flex;
  gap: 32px;
  margin-right: 24px;

  @media (min-width: $desktop-width) {
    margin-right: 64px;
  }
}

.header__item--boxes {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.header__nav-link {
  font-family: "Manrope";
  color: $black;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
  }

  &:hover {
    opacity: 0.7;
  }
}

.header__button-link {
  padding: 8px 18px;
  color: $white;
  font-size: 16px;
  line-height: 24px;
  background-color: $background-dark-green;
  border-radius: 32px;

  &:hover {
    opacity: 0.7;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    padding: 19px 64px;
  }
}
