.up-button {
  display: none;
  position: fixed;
  right: 16px;
  bottom: 16px;
  z-index: 10;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 16px;
  background-color: $light-grey;
  background-image: url("../Images/svg/up-button-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &:hover {
    opacity: 0.7;
  }

  @media (min-width: $desktop-width) {
    right: 64px;
    z-index: 10;
    width: 64px;
    height: 64px;
  }
}
