.questions {
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    margin-bottom: 8px;
  }
}

.questions__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 12px;
  text-align: center;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin: 0 0 40px 0;
  }
}

.questions__list {
  display: flex;
  flex-direction: column;
  gap: 4px;

  @media (min-width: $desktop-width) {
    gap: 8px;
  }
}

.questions__item {
  background-color: $light-grey;
  border-radius: 16px;

  @media (min-width: $desktop-width) {
    border-radius: 24px;
  }
}

.questions__subject {
  padding: 16px;
  display: flex;
  justify-content: space-between;
  gap: 8px;
  font-family: "Manrope";
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    padding: 40px;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
  }
}

.questions__answer {
  display: none;
  padding: 24px 16px;
  border-top: 1px solid $background-grey;
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    padding: 40px;
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
  }
}

.questions__item--open.questions__answer {
  display: block;
}

.questions__button {
  border: none;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: transparent;
  background-image: url("../Images/svg/questions-plus.svg");
  background-position: center;
  background-repeat: no-repeat;

   &:hover {
    opacity: 0.7;
  }

  @media (min-width: $desktop-width) {
    width: 32px;
    height: 32px;
  }
}

.questions__button--open {
  background-image: url("../Images/svg/questions-minus.svg");
}