.schedule-type__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  color: $light-grey;
  padding: 24px;
  background-color: $background-purple;
  border-radius: 20px 20px 0px 0px;

  @media (min-width: $desktop-width) {
    padding: 40px 20px;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
    text-align: center;
  }
}

.schedule-type {
  background-color: $background-grey;
  border-radius: 20px 20px 20px 20px;
  padding-bottom: 40px;
}

.schedule-type__list {
  padding: 24px 16px 16px 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;

  @media (min-width: $desktop-width) {
    padding: 40px 32px 32px 32px;
    gap: 16px;
  }
}

.schedule-type__item {
  font-family: "Manrope";
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  padding-left: 27px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 12px;
    left: 12px;
    width: 5px;
    height: 5px;
    background-color: $black;
    border-radius: 50%;
  }

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 30px;
    letter-spacing: -0.3px;
  }
}

.schedule-type__link {
  border-radius: 32px;
  padding: 11px 24px;
  text-align: center;
  background-color: $white;
  display: block;
  width: 240px;
  margin: 0 auto 0 24px;
  color: $black;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;

  &:hover {
    opacity: 0.7;
  }

  @media (min-width: $desktop-width) {
    padding: 19px 24px;
    width: 258px;
    margin: 0 auto 0 32px;
  }
}
